<template>
	<div id="rang_Tab">
		<div class="order-list">
			<div :class="{active:1 == tabIndex}" @click="orderBy(1)">综合</div>
			<div :class="{active:2 == tabIndex}" @click="orderBy(2)">评价</div>
			<div :class="{active:3 == tabIndex}" @click="orderBy(3)">销量</div>
			<div :class="[{active:4 == tabIndex},rang]" @click="orderBy(4)">价格</div>
		</div>
	</div>
</template>

<script>
import Bus from '@/bus.js' 
	const DEFAULT = 1;
	const COMMENT = 2;
	const SALE = 3;
	const DESC = 7; //价格倒序
	const ASC = 8;	//价格顺序
	const MSALE = 9;//月销量倒序
	
	export default {
		data() {
			return {
				tabIndex: 1,
				rang:'rang-icon-no',
				priceob : true,
			}
		},
		methods:{
			//筛选排序
			orderBy(arg) {
				let orderBy;
				this.tabIndex = arg;
				if(arg==4){
					if(this.priceob){
						orderBy = DESC
						this.rang = 'rang-icon-down'
						this.priceob = false;
					}else{
						orderBy = ASC
						this.rang = 'rang-icon-up';
						this.priceob = true;
					}
				}else{
					orderBy = arg
					
					this.rang = 'rang-icon-no';
					this.priceob = true;
					
				}
				this.$emit('sort',orderBy);
			}
		},
		components: {

		},

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#rang_Tab {
		.order-list {
			display: flex;
			font-size: 0.4rem;
			flex-direction: row;
			border-bottom: 1px solid $border-color;
			height: 1.07rem;
			line-height: 1.07rem;
			align-items: center;
			.active {
				color: $label-color;
			}
			>div {
				position: relative;
				height: 0.67rem;
				line-height: 0.67rem;
				text-align: center;
				flex: 1;
				border-right: 1px solid $border-color;
				&:last-child {
					border-right: none;
				}
			}
			.rang-icon-no {
				&:after {
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right: 0.53rem;
					top: 51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-no.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
			}
			.rang-icon-up {
				&:after {
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right: 0.53rem;
					top: 51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-up.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
			}
			.rang-icon-down {
				&:after {
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right: 0.53rem;
					top: 51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-down.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
			}
		}
	}
</style>


