<template>
	<div id="goods-list">
		<search-header>
			<router-link :to="{ name : 'Cart'}" slot="icon">
				<div class="cart">
					<img class="cart-icon" src="../../assets/cart-icon.png">
					<span class="num-icon">{{cartTotal?cartTotal:'0'}}</span>
				</div>
			</router-link>
		</search-header>
		
		<div class="tab-wrap">
			<rang-tab v-on:sort='sort'></rang-tab>
		</div>
		
		<div class="goods-wrap">
			<good-list :goodList="goodList"></good-list>
		</div>
	</div>
</template>

<script>
	import GoodList from '@/components/ChooseGift/GoodList'
	import SearchHeader from '@/components/Header/SearchHeader'
	import RangTab from '@/components/RangTab/RangTab'
	
	export default {
		data() {
			return {
				cartTotal:0,
				tabIndex: 1,
				rang:'rang-icon-no',
				priceob : true,
				PARAMS : {
					a:1,
					type:1,
					pa:1,
					li:10,
					ob:1,
					spuId:'',
					promId:'',
					categoryId:'',
					orgId:'',
					searchKey:'',
					lastId:''
				},

				goodList:[
//					{
//						skuId:1,
//						spuId:1,
//						type:1,
//						name:'商品名',
//						originalPrice:100,
//						discountPrice:50,
//						discount:5,
//						imagePath:'/static/img/pic.603b63b.png',
//						isDelete:2,
//						isUp:1,
//						tags:'加盟',
//						isSoldOut:2,
//						promId:1,
//						commentCount:1,
//						praiseCount:100
//					},
//					{
//						skuId:2,
//						spuId:2,
//						type:1,
//						name:'酷美人 夜光石手链 男女款萤石手链手串手环饰品 男女款萤石手链手串手环饰品',
//						originalPrice:200,
//						discountPrice:100,
//						discount:5,
//						imagePath:'/static/img/pic.603b63b.png',
//						isDelete:2,
//						isUp:1,
//						tags:'自营',
//						isSoldOut:2,
//						promId:1,
//						commentCount:1,
//						praiseCount:100
//					}
				],
			}
		},
		created(){
			this.getGoodsList()
//			this.getCartGoodsCount()			
			this.getUserDetails()
		},
		methods: {
			//排序
			sort(arg){
				this.PARAMS = Object.assign({},this.PARAMS,{
					ob:arg,
				})
				console.log(this.PARAMS)
				// this.goodList.push(obj);
			},
			paramsInit(){
				this.PARAMS = {
					a:this.$route.query.a?this.$route.query.a:1,
					type:this.$route.query.type?this.$route.query.type:1,
					pa:1,
					li:10,
					ob:this.$route.query.ob?this.$route.query.ob:1,
					spuId:this.$route.query.spuId,
					promId:this.$route.query.promId,
					categoryId:this.$route.query.categoryId,
					orgId:this.$route.query.orgId,
					searchKey:this.$route.query.searchKey,
					lastId:''
				}
			},
			getUserDetails(){
				this.$Api.UserDetails(0).then((res)=>{
					console.log(res)
					this.cartTotal = res.q.user.cartGoodsTotal
				})
			},
//			getCartGoodsCount(){
//				this.$Api.getCartGoodsCount(1).then((res)=>{
//					console.log(res)
//				})
//			},
			
			getGoodsList(){
				this.paramsInit()
				this.$Api.getGoodsList(this.PARAMS).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.goodList = res.q.goodses
					}
				})
			},
			
			
			
			
		},
		components: {
			GoodList,
			SearchHeader,
			RangTab
		},

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#goods-list {
		/*margin-top:1.17rem;*/
		.tab-wrap{
			position: fixed;
			top: 1.17rem;
			width: 100%;
			z-index: 100;
			background: #fff;
		}
		.goods-wrap{
			position: absolute; 
			top: 1.07+1.17rem;
			
		}
	}
</style>


